.App {
  text-align: center;
}

.line-break {
  width: 100%;
}

.content {
  flex-wrap: wrap;
}

pre {
  font-size: 0.9rem !important;
}

.small-screen {
  display: none;
}

@media screen and (max-width: 480px) {
  .small-screen {
    display: block;
  }
}

.big-screen {
  display: block;
}

@media screen and (max-width: 480px) {
  .big-screen {
    display: none;
  }
}

.image-container {
  display: flex;
  justify-content: center;
}
